import React from 'react'
import {Button, Icon, Modal, Select} from 'antd'
import {Link} from 'react-router-dom'
import './index.less'

const {Option} = Select;

export default class FrapStation extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visiblePostLinks: false,
        }
    }

    handleChange = (value) => {
        console.log(`selected ${value}`);
        localStorage.zd = value;
        window.location.reload();


    };

    render() {
        return (
            <div className='frap-station'>
                <Icon type="environment"/>
                
                <Select
                    defaultValue={localStorage.zd!==undefined?localStorage.zd:'1'}
                    style={{width: 120}}
                    onChange={this.handleChange}
                >
                    <Option value="1"></Option>
                    <Option value="2"></Option>
                    <Option value="3"></Option>
                </Select>
            </div>
        )
    }
}